export class FileInfo {
    id: string;
    owner: string;
    contentType: string;
    originalFileName: string;
    extension: string;
    content: string;
    fileUri: string;
    filePath: string;

    constructor() {
        this.id = '00000000-0000-0000-0000-000000000000';
        this.owner = '';
        this.originalFileName = '';
        this.extension = '';
        this.content = null;
        this.fileUri = '';
        this.contentType = '';
    }
}

export class FileType {
    static readonly IMAGE: string = "image";
    static readonly PDF: string = "pdf";
    static readonly EXCEL: string = "excel";
    static readonly XML: string = "xml";
    static readonly BINARY: string = "binary";
    static readonly TEXT: string = "text";
    static readonly GENERIC: string = "generic";
}
